.theme-toggle {
    position: absolute;
    top: 15px;
    left: 15px;

    --size: 2rem;

    background: none;
    border: none;
    padding: 0;

    inline-size: var(--size);
    block-size: var(--size);
    aspect-ratio: 1;
    border-radius: 50%;

    cursor: pointer;
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
    outline-offset: 5px;
}

.theme-toggle:hover {
    --size: 40px;
}

.sun-and-moon {
    inline-size: 100%;
    block-size: 100%;
    stroke-linecap: round;
}

.body-theme-dark {
    color: aliceblue;
    background-color:#09194a;
}

.body-theme-light {
    color: #09194a;
    background-color: aliceblue;
}